.item__points {
    background-color: #f5f1f1;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 15px;
    padding: 15px;
    border-radius: 6px;
}

.item__points li {
    font-size: 20px;
    padding: 5px;
    background-color: #fa8a52;
    border-radius: 6px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;

}