.popup-result__btn {
    width: 80%;
    margin: 0 auto;
    font-size: 16px;
    background-color: #e1e0e0;
    transition: background-color 0.3s ease 0s;
    text-transform: uppercase;
    padding-top: 3px;
    padding-bottom: 3px;
}

.popup-result__btn:hover {
    background-color: #cccaca;
}