.popup {
    background: #f8f1f9d4;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
}

.hidden {
    display: none;
}

.popup__body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
}

.popup__item {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    border-radius: 8px;
    border: 2px solid #bcbaba;
    padding: 25px;
    margin-bottom: 10px;
    background-color: white;
}

.popup__title {
    font-size: 16px;


}

.popup__text {
    font-size: 14px;
    max-width: 400px;
}

.popup__btn {
    width: 80%;
    margin: 0 auto;
    font-size: 16px;
    background-color: #e1e0e0;
    transition: background-color 0.3s ease 0s;
    text-transform: uppercase;
    padding-top: 3px;
    padding-bottom: 3px;
}

.popup__btn:hover {
    background-color: #cccaca;
}



.popup__icon {
    position: absolute;
    top: 10%;
    right: 10%;
    padding: 5px;
    border-radius: 6px;
    background-color: #ebe5e5;
    border: 1px solid #4b4a4a;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background-color 0.3s ease 0s;
}

.popup__icon:hover {
    background-color: #d5d0d0;

}

