
.popup__body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
}

.popup__item {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    border-radius: 8px;
    border: 2px solid #bcbaba;
    padding: 25px;
    margin-bottom: 10px;
    background-color: white;
}

.popup__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 25px;
}

.popup__line {
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.popup__label {
    text-align: center;
}

.popup__input {
    height: 35px;
    border-radius: 8px;
    border: #fa8a52 2px solid;
    text-align: center;
    font-size: 20px;
}