.popup-result {
    background: #f8f1f9d4;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
}

.popup-result__body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
}

.popup-result__item {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    border-radius: 8px;
    border: 2px solid #bcbaba;
    padding: 25px;
    margin-bottom: 10px;
    background-color: white;
}

.popup-result__content {
    display: flex;
    flex-direction: column;
    gap: 25px;
    align-items: center;
}

.popup-result__title {
    font-size: 16px;
}

.popup-result__text {
    font-size: 20px;
    max-width: 400px;
}



.popup-result__icon {
    position: absolute;
    top: 10%;
    right: 10%;
    padding: 5px;
    border-radius: 6px;
    background-color: #ebe5e5;
    border: 1px solid #4b4a4a;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background-color 0.3s ease 0s;
}